p {
  margin-top: 0;
  margin-bottom: 20px;
}

ul,
ol,
li {
  padding: 0px;
  margin: 0px;
  list-style: none;
}

ul, ol {
  margin-bottom: 20px;
}

h1,
h2,
h3,
h4,
h5,
.h1,
.h2,
.h3,
.h4,
.h5 {
  font-family: $baseFont;
  color: #000;
  text-transform: none;
  line-height: 1.42;
  font-weight: 700;
  margin-bottom: 20px;
}

h1,
.h1 {
  font-size: 2.5rem;
}

h2,
.h2 {
  margin-top: 30px;
  font-size: 2rem;
}

h3,
.h3 {
  margin-top: 30px;
  font-size: 1.375rem;
}

h4,
.h4 {
  margin-top: 30px;
  font-size: 1.125rem;
}

h5,
.h5 {
  margin-top: 30px;
  font-size: 1rem;
}

a {
  color: $linkColor;
  text-decoration: none;
  outline: none;
  position: relative;

  &.bold {
    &::after {
      height: 3px;
    }
  }

  &.off-hover {
    &::after {
      content: none;
    }
  }

  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -4px; /* Располагаем линию чуть ниже текста */
    width: 0;
    height: 1px;
    background-color: #fff; /* Цвет подчёркивания */
    transition: width .4s ease; /* Плавное увеличение ширины */
  }

  &:hover::after {
    width: 100%; /* При ховере растягиваем линию */
  }
}

img {
  max-width: 100%;
  height: auto;
}